import { configureStore } from '@reduxjs/toolkit';
import undoable from "redux-undo";
import {undoLimit, undoableActions} from "./common/undoableConsts";
// import counterReducer from '../features/counter/counterSlice';
import assFileReducer from "./AssFileSlice";
import lineOrderingReducer from "./LineOrderingSlice";
import lineRefsReducer from "./LineRefsSlice";
import uiStatesReducer from "./UIStatesSlice";
import selectedLinesReducer from "./SelectedLinesSlice";
import activeLineReducer from "./ActiveLineSlice";

export const store = configureStore({
  reducer: {
    // TODO: undoable, also remember ignoreInitialState
    assFile: undoable(assFileReducer, {
      limit: undoLimit,
      undoType: undoableActions.assFileUndo,
      redoType: undoableActions.assFileRedo,
      ignoreInitialState: true,
    }),

    lineOrdering: undoable(lineOrderingReducer, {
      limit: undoLimit,
      undoType: undoableActions.lineOrderingUndo,
      redoType: undoableActions.lineOrderingRedo,
      ignoreInitialState: true,
    }),
    lineRefs: lineRefsReducer,
    uiStates: uiStatesReducer,
    selectedLines: selectedLinesReducer,
    activeLine: activeLineReducer,
  },
});
